import Stack from '@mui/material/Stack';
import Slider from '@mui/material/Slider';
import Button from '@mui/material/Button';

import { useState, useEffect } from "react";

export default function MultiSimplexSlider( { onChange, defaultValue } ) {
    let [ControlFrac, setControlFrac] = useState(defaultValue[0]);
    let [MCIFrac, setMCIFrac] = useState(defaultValue[1]);
    let [DementiaFrac, setDementiaFrac] = useState(defaultValue[2]);

    useEffect(() => {
        onChange([ControlFrac, MCIFrac, DementiaFrac]);
    }, [ControlFrac, MCIFrac, DementiaFrac]);

    // given newValue, recompute equal porportions of A and B such that
    // the simplex is preserved
    const recomputeValue = (newValue, valueA, valueB) => {
        let remaining = 1-newValue;
        let aFrac = valueA/(valueA+valueB);

        let aNew = Number((remaining*aFrac).toFixed(2));
        let bNew = Number((remaining*(1-aFrac)).toFixed(2));

        return [aNew, bNew];
    };

    return (
        <Stack direction="column">
            <div style={{display: "flex"}}>
                <span className="text-right pr-5"
                      style={{transform: "translateY(3px)", flexGrow: 10}}>
                    <b>Control</b> 
                </span>
                <Slider aria-label="control" value={ControlFrac} style={{width: 150}}
                        onChange={(_, nv) => {
                            let [dementiaNew, mciNew] = recomputeValue(nv, DementiaFrac, MCIFrac);
                            setControlFrac(nv);
                            setDementiaFrac(dementiaNew);
                            setMCIFrac(mciNew);
                        }}
                        step={0.01} max={0.99} />
            </div>

            <div style={{display: "flex"}}>
                <span className="text-right pr-5"
                      style={{transform: "translateY(3px)", flexGrow: 10}}>
                    <b>MCI</b> 
                </span>
                <Slider aria-label="mci" value={MCIFrac} style={{width: 150}}
                        onChange={(_, nv) => {
                            let [dementiaNew, controlNew] = recomputeValue(nv, DementiaFrac, ControlFrac);
                            setMCIFrac(nv);
                            setDementiaFrac(dementiaNew);
                            setControlFrac(controlNew);
                        }} step={0.01} max={0.99} />
            </div>

            <div style={{display: "flex"}}>
                <span className="text-right pr-5"
                      style={{transform: "translateY(3px)", flexGrow: 10}}>
                    <b>Dementia</b>
                </span>
                <Slider aria-label="control" value={DementiaFrac} style={{width: 150}}
                        onChange={(_, nv) => {
                            let [controlNew, mciNew] = recomputeValue(nv, ControlFrac, MCIFrac);
                            setDementiaFrac(nv);
                            setControlFrac(controlNew);
                            setMCIFrac(mciNew);
                        }}
                        step={0.01} max={0.99} />
            </div>
        </Stack>
    );
}
