import './App.css';
import relabel from './relabel';
import data from "./data.json";

import MultiSimplexSlider from "./MultiSimplexSlider.jsx";
import Confusion from "./confusion.jsx";

import { useState, useEffect } from "react";

function App() {
    let [thresholds, setThresholds] = useState([0.33, 0.33, 0.33]);
    let [labels, setLabels] = useState(relabel(data, thresholds));
    useEffect(() => {
        setLabels(relabel(data, thresholds));
    }, [thresholds]);

    return (
        <div className="flex items-center justify-center w-screen h-screen">
            <div>
                <Confusion confusion={labels[0]}/>
                <div className="w-60">
                    <MultiSimplexSlider
                        defaultValue={[0.33, 0.33, 0.33]}
                        onChange={(r) => setThresholds(r)}
                    />
                </div>
                <div style={{textAlign: "center", marginTop: "15px", fontFamily: "monospace"}}>
                    [{thresholds.join(", ")}]
                </div>
                <div style={{textAlign: "center", marginTop: "4px", fontFamily: "monospace"}}>
                    accuracy: {labels[1].toFixed(2)}
                </div>
            </div>
        </div>
    );
}

export default App;
