// get index of the largest value
function largestIndex(array){
    var counter = 1;
    var max = 0;

    for (counter = 0; counter < array.length; counter++) {
        if (array[max] < array[counter]) {
            max = counter;
        }
    }
    return max;
}

export default function relabel(data, thresholds) {
    // compute the labels
    let labels = data.labels.map((x) => x.indexOf(1));
    // compute the threshold (we reverse thresholds to
    // make pulling stuff "up" mean increasing threshold)
    let thresholded = data.logits.map((x) =>
        largestIndex(x.map((y, i) => y/(1-thresholds[i]))));
    // create the confusion matrix
    let confusion = [[0,0,0],
                     [0,0,0],
                     [0,0,0]];
                     // true on the first axis, pred on the second axis
    labels.forEach((x, i) => confusion[x][thresholded[i]] += 1 );
    // normalize the confusion matrix, for each true label compute
    // what percentage was sent to what
    confusion = confusion.map((i) => {
        let sum = i.reduce((a, b) => a + b, 0);
        return i.map((j) => j/sum);
    });
    // calculate accuracy
    let accuracies = thresholded.map((x, i) => Number(labels[i] == x));
    return [confusion, accuracies.reduce((a,b) => a+b, 0)/accuracies.length];
}
