import "./confusion.css";

export default function Confusion({confusion}) {
    return (
        <div className="confusion-container">
            <div className="confusion">
                <span style={{gridColumn: 1,
                              gridRow: 1}}></span>
                <span style={{gridColumn: "2/5",
                              gridRow: 1}}
                      className="label">Predicted</span>
                <span style={{gridRow: "2/5",
                              gridColumn: 1}}
                      className="label">
                    <span style={{transform: "rotate(180deg)",
                                  writingMode: "vertical-rl"}}>Actual</span>
                </span>
                {confusion[0].concat(confusion[1], confusion[2]).map((i, indx) => {
                    return <span key={indx}
                                         style={{ backgroundColor: `rgba(111, 151, 217, ${i})` }}
                                         className="confusion-item">{i.toFixed(2)}</span>;
                })}
            </div>
        </div>

    );
}


